


























































import { Component, Prop, Vue } from 'vue-property-decorator';
import PickerOne from '@/application/Common/View/CommonPickerOne.vue';
import { GameData } from '@/globalInterface';

@Component({
  components: {
    PickerOne
  }
})
export default class BuyPlanView extends Vue {
  private showpopup?: boolean = false;
  private issueCount?: number = 47;
  private gameList?: GameData[] = [
    {
      id: 7,
      text: '北京PK10',
    },
    {
      id: 10,
      text: 'SG飞艇'
    }
  ];
  private indexChange(value: GameData) {
    this.$emit('index-change', value);
  }
  private togglePopup() {
    this.showpopup = !this.showpopup;
  }
}
